/* App's static wrapper */

.App {
    text-align: center;
}

#Grovv-Logo {
    pointer-events: none;
    width: auto;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.App-body {
    background-color: #5a5252;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
    font-size: calc(10px + 2vmin);
    color: white;
}

#App-Header {
    position: fixed;
    overflow: hidden;
    top: 0;
    height: 100px;
    width: inherit;
    background-color: #317d92;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

#Footer-Btns {
    /* padding-top: 2px; */
    position: absolute;
    padding-bottom: 2px;
    left: 64px;
    top: 70px;
    display: flex;
    justify-content: flex-end;
}

