/* Journal Page */

#Journal-Page {
    width: 100%;
    height: 100%;
}

#Journal-Main {
    background: rgb(12, 68, 21);
    color: #FFF;
    font-size: 18px;
    display: flex;
    flex-direction: column;
    font-weight: bolder;
    position: absolute;
    width: 100%;
    height: 100%;
    align-content: center;
    text-align: center;
}

#Timeline-Container {
    position: relative;
    margin-left: 2.5%;
    margin-right: 2.5%;
    margin-bottom: 2.5%;
}

#Timeline-Line {
    width: 98%;
    height: 6px;
    position: relative;
    background-color: #ceac16;
    top: 50%;
}

#Timeline-Spots {
    width: 100%;
    height: 90%;
    position: absolute;
    top: -200%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#Timeline-Add-Dot {
    height: 40px;
    width: 40px;
    background-color: #191466;
    border-radius: 50%;
    /* margin-left: 96%; */
    /* top: 15%; */
    bottom: 6%;
    position: relative;
    color: #FFFFFF;
    user-select: none;
}

.Timeline-Dot {
    height: 33px;
    width: 33px;
    background-color: #0afc0a;
    border-radius: 50%;
    /* margin-left: 96%; */
    /* top: 20%; */
    position: relative;
    color: #FFFFFF;
    user-select: none;
}

.Timeline-Dot:hover {
    background-color: #bbfc0a;
}

.Timeline-Dot:active {
    background-color: #aecfae;
}

#Timeline-Add-Dot:hover {
    background-color: #302a8a;
}

#Timeline-Add-Dot:active {
    background-color: #6962ca;
}


/* JOURNAL MODAL */

#Journal-Modal-Space {
    width: 100%;
    height: 100%;
    z-index: 9999999;
    background-color: #000000a9;
    position: absolute;
}

#Journal-Edit-Entry-Modal {
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 90%;
    margin: 0 auto;
    margin-top: 4%;
    background-color: #29a343;
    padding: 6px;
}

.journal-modal-edit-date {
    display: flex;
    flex-direction: row;
}

.modal-date-li {
    font-size: 16px;
    color: #fff;
    background: darkorange;
    display: block;
    float: left;
    padding: 4px;
    position: relative;
    text-decoration: none;
    transition-duration: 0.5s;
}

.modal-date-li a {
    color: #fff;
}

.modal-date-li:hover {
    background: red;
    cursor: pointer;
}

.journal-modal-edit-date {
    background: darkorange;
    list-style: none;
    margin: 0;
    padding-left: 0;
    width: 200px;
}

.journal-entry-date-dropdown {
    background: orange;
    visibility: hidden;
    opacity: 0;
    min-width: 5rem;
    position: absolute;
    transition: all 0.5s ease;
    left: 0;
    display: none;
}

.date-li {
    clear: both;
    width: 100%;
    font-size: 16px;
}

.modal-date-li:hover>ul,
.modal-date-li:focus-within>ul,
.journal-entry-date-dropdown:hover {
    visibility: visible;
    opacity: 1;
    display: block;
}


#Journal-Edit-Topline {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly
}

.journal-modal-edit-stage {
    display: flex;
    flex-direction: row;
}

.modal-stage-li {
    font-size: 16px;
    color: #fff;
    background: darkorange;
    display: block;
    float: left;
    padding: 4px;
    position: relative;
    text-decoration: none;
    transition-duration: 0.5s;
}

.modal-stage-li a {
    color: #fff;
}

.modal-stage-li:hover {
    background: red;
    cursor: pointer;
}

.journal-modal-edit-stage {
    background: darkorange;
    list-style: none;
    margin: 0;
    padding-left: 0;
    width: 200px;
}

.journal-entry-stage-dropdown {
    background: orange;
    visibility: hidden;
